import React, {useEffect} from 'react'
import './Describe.scss'
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Aos from 'aos';
import 'aos/dist/aos.css'


export default function Describe() {

  const style = {
    paper: {
      padding: '0.4em 0.5em'
    }
  }

  useEffect (()=> {
    Aos.init({duration: 3000})
  }, [])


  return (
    <div>
        
                <div className='describe'>
                    <div className="describe__box">
                        <div className="describe__box-text">
                            <Box
                              sx={{
                                display: 'flex',
                                justifyContent: 'center',
                                '& > :not(style)': {
                                  m: 1,
                                  width: '80%',
                                  height: '100%',
                                },
                              }}
                            >
                              <Paper elevation={3} style={style.paper} data-aos="fade-right" >
                                Aplikacja pobiera aktualne dane o kryptowalutach i pokazuje najnowsze dane odnośnie cen.
                               Zapoznaj się ze szczegółowymi informacjami.
                              </Paper>
                            </Box>
                        </div>
                    </div>
                </div>
        
    </div>
  )
}
