import React, { useEffect, useState } from 'react'
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import './Coin.scss'
import axios from 'axios';
import { useParams } from 'react-router-dom';
import DOMPurify from 'dompurify';


export default function Coin() {

  const style = {
    paper: {
      border: '1px solid #4e42d4', 
    }
  }

  const [oneCoin, setoneCoin] = useState([])
  const params = useParams()


  const one_url = `https://api.coingecko.com/api/v3/coins/${params.coinId}`

 

  useEffect(()=> {
    const getCoin = async () => {
      const {data: res} = await axios.get(one_url)
      setoneCoin(res)
    } 
    getCoin()
  }, [])

 console.log(oneCoin)

  return (
    <div>    
            <div>
            <Box  
                              sx={{
                                display: 'flex',
                                justifyContent: 'center',
                                '& > :not(style)': {
                                  m: 1,
                                  width: '80%',
                                  height: '100%',
                                },
                              }}
                            >
                              <Paper elevation={3} style={style.paper}>
                                <div className="paper__first"><div className="paper__first-nav">Nazwa:</div><span className='paper____first-name--detal'> {oneCoin.name}</span>
                                <div className='paper__first-img'>
                                {
                                oneCoin.image ? <img src={oneCoin.image.small} alt="ikony" /> : null
                               }
                                </div>
                                </div>

                                <div className="paper__second"><div className="paper__second-nav">Aktualna cena: </div><span className='paper__second-nav--detal'>{ oneCoin.market_data ? oneCoin.market_data.current_price.pln.toFixed(2) + ' PLN' : null}</span>
                                </div>

                                <div className="paper__third"><div className="paper__third-nav">Zmiana 24h cena: </div><span className='paper__third-nav--detal'>{ oneCoin.market_data ? oneCoin.market_data.price_change_24h_in_currency.pln.toFixed(2) + ' PLN' : null}</span>
                                </div>

                                <div className="paper__describe">
                                <div className='paper__describe-nav'>Opis: </div>
                                  <p dangerouslySetInnerHTML=
                                  {{__html: DOMPurify.sanitize(oneCoin.description ?oneCoin.description.pl : null),}}
                                  >
                                  </p>
                                </div>
                                
                              </Paper>
                            </Box>
              

            </div>  
    </div>
  )
}
